.btn {
	width: 100%;
	height: 40px;
	text-align: center;
	color: $white;
	box-sizing: border-box;
	line-height: 40px;
	text-transform: uppercase;
	@include LatoFont;
	display: block;
	border: 0;
	outline: 0;
	text-decoration: none;
	border-radius: 3px;
	transition: all 250ms;
	cursor: pointer;

	&-blue {
		background: $blue;

		&:hover {
			opacity: .95;
		}
	}

	&-red {
		background: $red;

		&:hover {
			opacity: .95;
		}
	}

	&-add {
		width: 40px;
		background: $bgColor;
		color: $secondary;
		text-align: center;
		line-height: 40px;

		&:hover {
			color: $primary;
		}
	}
}